import React, { useState, useEffect } from 'react';
import axios from 'axios';
import cheerio from 'cheerio';

const RestaurantList = () => {
    const [restaurants, setRestaurants] = useState([]);
    const [url, setUrl] = useState('');
    const [selectedPhotos, setSelectedPhotos] = useState([]);

    const fetchData = async (url) => {
        const result = await axios(url);

        const $ = cheerio.load(result.data);
        const items = $('div.rstdtl-header-wrap').toArray();
        const restaurantData = items.map(item => {
            const rating = $(item).find('.rdheader-counts-wrap').find('.rdheader-rating__score-val-dtl').text().trim();
            const price_dinner = $($(item).find('.rdheader-budget__price-target')[0]).text().trim();
            const price_lunch = $($(item).find('.rdheader-budget__price-target')[1]).text().trim();
            const closest_station = $(item).find('.rdheader-subinfo__item--station').find('.linktree__parent-target-text').text().trim();
            const area = $(item).find('.rdheader-subinfo__item').find('.linktree__parent-target').text().trim();
            console.log(area)
            return { rating, price_dinner, price_lunch, closest_station, area };
        });

        const items2 = $('div.rstdtl-rstdata').toArray();
        const restaurantData2 = items2.map(item => {
            const name = $(item).find('tr:contains("") span').text().trim();
            const category = $(item).find('tr:contains("") span').text().trim();
            const business_hours = $(item).find('.rstinfo-table__subject:contains("")').next().text().trim();
            const regular_holiday = $(item).find('.rstinfo-table__subject:contains("")').next().text().trim();
            const showInput = false;
            const favorite_point_input = "";
            const photos = [];
            return { name, category, business_hours, regular_holiday, showInput, favorite_point_input, photos };
        });
        const merged = [{ ...restaurantData[0], ...restaurantData2[0] }];

        // 
        const existingRestaurant = restaurants.find(restaurant => restaurant.name === merged[0].name);
        if (existingRestaurant) {
            alert('');
            return;
        }

        // URL
        setRestaurants([...merged, ...restaurants]);
    };


    const handleSubmit = (e) => {
        e.preventDefault();
        const newUrl = new URL(e.target.elements.url.value);
        const newPath = newUrl.pathname;

        // URL
        const existingUrl = restaurants.find(restaurant => restaurant.url === newPath);
        if (existingUrl) {
            return;
        }

        // URL
        fetchData(newPath);

        // URL
        setUrl('');
    };
    const toggleInput = (index) => {
        const updatedRestaurants = [...restaurants];
        updatedRestaurants[index].showInput = true;
        setRestaurants(updatedRestaurants);
    };

    const handleInputChange = (index, event) => {
        const updatedRestaurants = [...restaurants];
        updatedRestaurants[index].favorite_point_input = event.target.value;
        setRestaurants(updatedRestaurants);
    };

    const saveFavoritePoint = (index) => {
        const updatedRestaurants = [...restaurants];
        updatedRestaurants[index].favorite_point = "" + updatedRestaurants[index].favorite_point_input;
        updatedRestaurants[index].showInput = false;
        setRestaurants(updatedRestaurants);
    };

    const editFavoritePoint = (index) => {
        const updatedRestaurants = [...restaurants];
        updatedRestaurants[index].showInput = true;
        updatedRestaurants[index].favorite_point_input = updatedRestaurants[index].favorite_point.replace("", "");
        setRestaurants(updatedRestaurants);
    };

    const handlePhotoChange = (index, event) => {
        const updatedRestaurants = [...restaurants];
        const selectedPhotos = Array.from(event.target.files);
        updatedRestaurants[index].photos = selectedPhotos;
        setRestaurants(updatedRestaurants);
    };

    const Photo = ({ photo }) => {
        return <img src={URL.createObjectURL(photo)} alt="" />;
    };
    return (
        <div className="App">
            <div className="App-header container-fluid bg-primary text-light py-5">
                <div className="row align-items-center">
                    <div className="title col-12 col-md-8 mx-auto">
                        <h1 class="display-4 text-center mb-4 text-nowrap center-block">Tablog Restaurant List</h1>
                        <form onSubmit={handleSubmit}>
                            <div className="form-group row justify-content-center">
                                <label htmlFor="url" className="col-2 col-form-label">URL</label>
                                <div className="col-6">
                                    <input type="text" id="url" name="url" value={url} onChange={(e) => setUrl(e.target.value)} className="form-control" />
                                </div>
                                <div className="col-2 mt-0">
                                    <button type="submit" className="btn btn-light btn-block text-nowrap"></button>
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
            <div className="container py-5">
                <div className="row">
                    {restaurants.map((restaurant, index) => (
                        <div className="col-12 col-md-6 mb-4" key={index}>
                            <div className="card">
                                <div className="card-body">
                                    <h2 className="card-title">{restaurant.name}</h2>
                                    <p className="card-text">{restaurant.category}</p>
                                    <p className="card-text">{restaurant.closest_station}</p>
                                    <p className="card-text">{restaurant.rating}</p>
                                    <p className="card-text">{restaurant.price_lunch}{restaurant.price_dinner}</p>
                                    <p className="card-text">{restaurant.business_hours}</p>
                                    <p className="card-text">{restaurant.regular_holiday}</p>
                                    <div className="justify-content-between align-items-center">
                                        <div className="ml-2 col-10 mb-4">
                                            {
                                                restaurant.showInput ?
                                                    <div className="input-group">
                                                        <input type="text" value={restaurant.favorite_point_input} onChange={(event) => handleInputChange(index, event)} className="form-control" />
                                                        <div className="input-group-append">
                                                            <button onClick={() => saveFavoritePoint(index)} className="btn btn-outline-primary save-button"></button>
                                                        </div>
                                                    </div>
                                                    :
                                                    restaurant.favorite_point ?
                                                        <div>
                                                            <ul className="favorite_point">
                                                                <li className="favorite_point_list">{restaurant.favorite_point}</li>
                                                                <li className="favorite_point_list favorite_point_list_button"><button onClick={() => editFavoritePoint(index)} className="btn btn-light btn-block"></button></li>
                                                            </ul>
                                                        </div>
                                                        :
                                                        <button onClick={() => toggleInput(index)} className="btn btn-light btn-block"></button>

                                            }
                                        </div>
                                        <div className="col-12 col-md-6 mb-4">
                                            <input type="file" accept="image/*" multiple onChange={(event) => handlePhotoChange(index, event)} />
                                            <ul>
                                                {restaurant.photos.map((photo, index) => (
                                                    <li key={index}>
                                                        <Photo photo={photo} />
                                                    </li>
                                                ))}
                                            </ul>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    ))}
                </div>
            </div>
        </div>
    );

};
export default RestaurantList;